@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,500;1,500;1,600&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: 0;
}

html,
body {
  font-size: 62.5%;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

hr {
  width: 90%;
  background-color: #9c9898;
  margin: 10px 0 0 10px;
}

p.gituser {
  margin: 10px auto 10px;
  background-color: #555;
  font-size: 1.7rem;
  border-radius: 10px;
  padding: 0 10px;
  color: #f5f5f5;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 100%;
  height: 80px;
  margin: auto;
  background-color: #333;
}

header div.menu-icon,
header div.bars {
  width: 100%;
  display: flex;
  font-size: 2rem;
  color: #e9e9e9;
  align-items: center;
  justify-content: center;
}

header nav ul {
  display: flex;
}

header nav ul li {
  list-style: none;
}

header nav ul li a {
  color: #e9e9e9;
  text-decoration: none;
  padding: 20px;
  opacity: 0.7;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}

header div.menu-icon a {
  text-decoration: none;
  color: #e9e9e9;
  font-weight: bold;
  font-size: 3rem;
  opacity: 0.7;
  margin-left: 15px;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
}

header div.menu-icon a:hover,
header nav ul li a:hover {
  opacity: 1;
}

@media (max-width: 540px) {
  .brand {
    font-size: 2rem;
  }
  nav {
    display: none;
  }
  .one,
  .two,
  .three {
    background-color: #fff;
    height: 5px;
    width: 100%;
    margin: 6px auto;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
  }

  .menu-toggle {
    width: 40px;
    height: 30px;
    margin-right: 20px;
  }
  .bars.on {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #333;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .bars.on nav {
    display: block;
  }

  .bars.on .menu-toggle {
    position: absolute;
    right: 25px;
    top: 15px;
  }

  .bars.on .menu-toggle .one {
    -webkit-transform: rotate(45deg) translate(7px, 7px);
            transform: rotate(45deg) translate(7px, 7px);
  }

  .bars.on .menu-toggle .two {
    opacity: 0;
  }

  .bars.on .menu-toggle .three {
    -webkit-transform: rotate(-45deg) translate(8px, -9px);
            transform: rotate(-45deg) translate(8px, -9px);
  }

  .bars.on nav ul {
    display: block;
    text-align: center;
  }

  .bars.on nav ul a {
    display: block;
    font-size: 3rem;
    line-height: 4rem;
    -webkit-transition-duration: 0.5s;
            transition-duration: 0.5s;
  }
}

div.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-height: 100%;
  height: 100%;
}

.link {
  margin-top: 10px;
  padding: 10px 20px;
  border-radius: 25px;
  background-color: #00ff66;
  text-decoration: none;
  text-align: center;
  color: #fff;
  opacity: 0.7;
  font-size: 1.7rem;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.link:hover {
  opacity: 1;
  color: #333;
}

body {
  background-color: #555;
  color: #00ff66;
}

div.about section.profile {
  margin-top: 20px;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(153, 153, 153, 0.5);
  border-radius: 10px;
}

div.about section.profile {
  color: #f5f5f5;
}

section.profile p {
  font-size: 1.7rem;
}

div.intention {
  margin-top: 20px;
  padding: 20px;
  border-radius: 10px;
  align-items: center;
  justify-content: center;
  background-color: rgba(153, 153, 153, 0.5);
  text-align: center;
}

div.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

div.about section.profile img {
  width: 50%;
  height: 50%;
  border-radius: 50%;
}

div.about section.skills {
  margin-top: 20px;
  padding: 20px 80px 20px;
  border-radius: 10px;
  background-color: rgba(153, 153, 153, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: left;
}
div.about section.skills h1 {
  text-align: center;
  color: #f5f5f5;
  margin-bottom: 10px;
}

div.about section.skills ul {
  color: rgba(117, 255, 172, 1);
  font-size: 1.7rem;
}

div.intention p {
  margin: 10px auto 15px;
  color: #f5f5f5;
}

div.intention h2 {
  margin: 10px auto 15px;
  color: #f83f07;
  font-size: 2rem;
}

@media (max-width: 1024px) {
  div.intention {
    margin: 20px 20px;
  }
}

@media (max-width: 455px) {
  div.about,
  div.intention,
  section.profile,
  section.skills,
  .contact {
    border-radius: 0 !important;
    background-color: #999;
  }
  .contact ul li {
    opacity: 1 !important;
  }

  .contact ul li:hover {
    opacity: 1 !important;
    -webkit-transform: scale(1) !important;
            transform: scale(1) !important;
  }
  .mail {
    font-size: 1.2rem;
  }
}

.contact {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 30px;
  background-color: rgba(153, 153, 153, 0.5);
  border-radius: 15px;
}

.contact h1 {
  color: #8c238a;
  text-align: center;
  text-shadow: 1px 1px 3px #f5f5f5;
}

.contact ul {
  list-style: none;
  margin-top: 15px;
}

.contact ul li {
  display: flex;
  align-items: center;
  border-radius: 7px;
  margin-top: 10px;
  padding: 10px;
  background-color: #fff;
  opacity: 0.7;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.contact ul li p {
  margin-left: 10px;
  color: #8c238a;
  text-decoration: none;
  font-size: 2rem;
}
@media (max-width: 465px) {
  div.intention {
    margin: 20px auto 20px;
  }
  .contact ul li p {
    font-size: 1.7rem !important;
  }
}
@media (max-width: 465px) {
  .contact ul li p {
    font-size: 1.4rem !important;
  }
  section.skills h1 {
    font-size: 2rem;
  }
}

.contact ul a {
  text-decoration: none;
}

.contact li:hover {
  opacity: 1;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.mail {
  cursor: pointer;
}

div.intention p.description {
  font-size: 1.7rem;
}

div.content {
  max-width: 100%;
  width: 100%;
}
.repos {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  width: 70%;
  margin-top: 20px;
  padding: 20px;
  border-radius: 15px;
  background-color: rgba(153, 153, 153, 0.5);
}

h1 {
  text-align: center;
  margin: 10px 0 0 10px;
  font-size: 3.7rem;
  color: #f5f5f5;
}

div.repo-item {
  padding: 10px 20px;
  background-color: rgb(245, 245, 245, 0.8);
  border-radius: 10px;
  margin: 10px;
  color: #333;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

div.repo-item:hover {
  background-color: rgb(245, 245, 245, 1);
}

div.repo-item h2 {
  font-size: 2rem;
  font-weight: bolder;
  margin-bottom: 5px;
}

p.language {
  color: #ff8400;
  margin-bottom: 5px;
  font-size: 2rem;
}

p.description {
  color: #444;
  margin-bottom: 5px;
  font-size: 2rem;
}

div.button a {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 10px;
  background-color: #00ff1e;
  opacity: 0.7;
  text-decoration: none;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  font-size: 2rem;
}

div.button a:hover {
  opacity: 1;
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

div.button p {
  margin-left: 5px;
  color: #333;
}

@media (max-width: 530px) {
  p.description,
  p.language,
  div.button a {
    font-size: 1rem;
  }
  .repos {
    border-radius: 0px;
    width: 100% !important;
    padding: 10px 0 0 0;
  }
}

div.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  width: 100%;
}

div.content-profile {
  padding: 20px;
  margin: 20px 0 20px 0;
  border-radius: 15px;
  width: 60%;
  background-color: rgba(153, 153, 153, 0.5);
}

div.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

div.contact-profile {
  border-radius: 0px;
  background: none;
}

div.profile img {
  width: 50%;
  height: 50%;
  border-radius: 50%;
}

div.contact-profile h1 {
  color: #f5f5f5;
}

div.content-profile p {
  font-size: 1.7rem;
  text-align: center;
  color: #f5f5f5;
}

@media (max-width: 768px) {
  div.content-profile {
    width: 100%;
    border-radius: 0px;
  }
}

@media (max-width: 630px) {
  div.contact-profile h1 {
    font-size: 2.7rem;
  }
}

@media (max-width: 425px) {
  div.profile h1 {
    font-size: 2.6rem;
  }
}

div.description p {
  color: #23d400;
  font-size: 1.8rem !important;
}

p.job {
  margin: 10px 0 10px 0;
  font-size: 2.4rem !important;
  color: #f00 !important;
  font-family: "Jost", sans-serif;
  font-style: italic;
  font-weight: bold;
}

div.container-error {
  max-width: 100%;
  width: 100%;
  display: flex;
  margin-top: 20px;
  justify-content: center;
  height: 80vh;
}

div.content-error {
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

div.content-error h1 {
  color: #ff3232;
}

div.content-error p {
  color: #999999;
  font-size: 1.7rem;
  margin: 20px 0 20px 0;
  text-align: center;
}

.redirect-button {
  color: #fff;
  font-size: 1.8rem;
  text-decoration: none;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
  padding: 10px 30px 10px 30px;
  width: 60%;
  border-radius: 10px;
  background-color: #0f6;
  border: 1px solid #0f6;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.redirect-button:hover {
  background-color: #fff;
  color: #333;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media (max-width: 470px) {
  div.content-error {
    border-radius: 0px;
    width: 100%;
  }
}

